import React, { useState } from 'react'
import { Button, Container, Image, Nav, Navbar, Offcanvas, Stack } from 'react-bootstrap'
import Heading from './heading'
import Paragraph from './paragraph'

import Space from './space'

import CustomButton from './button'
import Heading2 from './heading2'
import { AnimateComponent } from '../utils/animation'
import { backgroundColor, mutedBackground, primaryColor, secondaryColor, textColor } from '../utils/company_colors'
import { animated } from 'react-spring'
import { BsPhone, BsPhoneFill, BsPhoneLandscape, BsPhoneLandscapeFill } from 'react-icons/bs'

import { AiFillMail, AiFillPhone, AiOutlineClose, AiOutlineFacebook, AiOutlineInstagram, AiOutlineLinkedin, AiOutlineTwitter } from 'react-icons/ai'
export const links = [{name:"HOME",path:"#about"},{name:"ABOUT US",path:"#about"},{name:"SERVICES",path:"#services"},{name:"WHY US ?",path:"#why"}];

const NavigationBar = () => {
    const navAnimation = AnimateComponent({from:{x:0},to:{x:20}})
const [showMenu, setShowMenu] = useState(false);
const [currentNav, setCurrentNav] = useState(0);
    return (
       
       <>
       {/* <div style={{width:"100%",backgroundColor:textColor,height:20}}></div> */}
      
        <Space height={20}/>
        <Container >
       <Stack direction='horizontal' className=' d-flex justify-content-end'>
       <Paragraph fontSize={`1vw`} className={`small ms-auto`} color={'gray'}  text={`Mikocheni B, Dar es salaam, Tanzania`}/>  
             <Paragraph fontWeight={700} fontSize={`1vw`}  className={`small ms-3 me-4`} color={secondaryColor}  text={`View on map`}/>  
         
             
             

        </Stack>
       </Container>
       <Navbar   className=' m-0 mt-3 py-0 d-flex align-items-center ' expand="md" >
     
        <Container  className='py-0 my-0 d-flex align-items-center' >
        <Navbar.Brand >
            <Stack direction='horizontal'>
             <Heading2 className={`ms-2`}  fontWeight={900} color={primaryColor}   text={`HakiMawasiliano`}/>

            </Stack>
        </Navbar.Brand>
        <Navbar.Toggle onClick={()=>setShowMenu(true)} className='border-0 shadow-none'></Navbar.Toggle>
        <Navbar.Collapse className='ms-auto d-none d-md-block'>
            <Offcanvas show={showMenu} onHide={()=>setShowMenu(false)}>
                <Space/>
                <Offcanvas.Header className='d-flex justify-content-end'>
                    
                  <AiOutlineClose onClick={()=>setShowMenu(false)} color={secondaryColor} size={40}/>
                </Offcanvas.Header>
                <Offcanvas.Body>
                {
                links.map((item)=>{
                  return <Nav.Link href={item.path} className='me-3 mb-3'><Paragraph color={`white`}  style={{...navAnimation.styles}} text={item.name}/></Nav.Link>
                })
            }
                </Offcanvas.Body>
            </Offcanvas>
        <Nav className='ms-auto d-flex align-items-center'>
            {
                links.map((item,index)=>{
                  return <Nav.Link href={item.path}  className='me-4'><Paragraph fontSize={`1vw`} fontWeight={currentNav == index? 700:700}  
                  color={primaryColor} style={{...navAnimation.styles,fontWeight:700}} text={item.name}/></Nav.Link>
                })
            }
         <Nav.Link    className='me-3 btn ' style={{borderRadius:30,padding:"10px 8px",backgroundColor:secondaryColor}}><Paragraph fontSize={`1vw`} color={backgroundColor} className={`px-2`} style={{...navAnimation.styles}} text={`Contact Us`}/></Nav.Link>
        </Nav>
        </Navbar.Collapse>

        </Container>

       </Navbar>
       {/* <Space height={20}/> */}


       </>
    )
}

export default NavigationBar
